import React, { useState, useContext } from "react";
import StyleContext from "../../contexts/StyleContext";
import "./LanguageSwith.css";

const LanguageSwith = () => {
  const { isEnglish } = useContext(StyleContext);
  const [isChecked, setChecked] = useState(isEnglish);
  const styleContext = useContext(StyleContext);

  return (
    <label className="switch">
    <input
      type="checkbox"
      checked={isEnglish}
      onChange={() => {
        styleContext.changeLanguage();
        setChecked(!isChecked);
      }}
    />
    <span className="slider round"></span>
    <span className="select-fr">FR</span>
    <span className="select-en">EN</span>
  </label>
  );
};
export default LanguageSwith;
